import { profileAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    posts: [
        { id: 1, message: ',  ?', likes: '2' },
        { id: 2, message: ',   ', likes: '1' },
        { id: 3, message: '  :)', likes: '12' },
    ],
    profile: null,
    status: '',
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {

        case ADD_POST: {
            let newPost = {
                id: 5,
                message: action.newPostText,
                likes: 0,
            };

            return {
                ...state,
                posts: [...state.posts, newPost],
                NewPostText: '',
            };
        }

        case SET_USER_PROFILE: {
            return { ...state, profile: action.profile }
        };

        case SET_STATUS: {
            return { ...state, status: action.status }
        };
            
        case DELETE_POST: {
            return { ...state, posts: state.posts.filter(el => el.id != action.id)}
        };

        default:
            return state;
    }
};

export const addPostActionCreator = (newPostText) => ({ type: ADD_POST, newPostText});

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });

export const setStatus = (status) => ({ type: SET_STATUS, status });

export const deletePost = (id) => ({ type: DELETE_POST, id });

export const getUserProfile = (profileId) => {return async (dispatch) => {
    let data = await profileAPI.RenderMyProfille(profileId)
            dispatch(setUserProfile(data));
        };
    }


export const getStatus = (profileId) => { return async (dispatch) => {
    let data = await profileAPI.getStatus(profileId)
            dispatch(setStatus(data));
        };
    }


export const updateStatus = (status) => (dispatch) => {
    profileAPI.updateStatus(status)
        .then(data => {
            if (data.resultCode === 0) {
                dispatch(setStatus(status));
            }
        }
        )
}

export default profileReducer;