import React from 'react';
import cl from './ProfileInfo.module.css';
import PreloaderLarge from '../../../UI/preloader/PreloaderLarge.jsx';
import AvatarNotSelected from '../../../assets/images/AvatarNotSelected.png';
import galochka from '../../../assets/images/galochka.png';
import krest from '../../../assets/images/krest.png';
import facebook from '../../../assets/svg/facebook.svg';
import github from '../../../assets/svg/github.svg';
import instagram from '../../../assets/svg/instagram.svg';
import mainLink from '../../../assets/svg/mainLink.svg';
import twitter from '../../../assets/svg/twitter.svg';
import vk from '../../../assets/svg/vk.svg';
import website from '../../../assets/svg/website.svg';
import youtube from '../../../assets/svg/youtube.svg';
import ProfileStatusWithHooks from './ProfileStatus';

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <PreloaderLarge />
    }
    return (
        <div>
            <div className={cl.wrapperImg}>
                <img className={cl.background} src='https://t4.ftcdn.net/jpg/05/08/88/13/360_F_508881346_Y8NPkWuFIspsIucnv2mn43U8FcLxL3XB.jpg'></img>
            </div>
            <div className={cl.profileInfo}>
                <div>
                    <img className={cl.avatar} src={props.profile.photos.large != null || undefined ? props.profile.photos.large : props.profile.photos.small != null || undefined ? props.profile.photos.small : AvatarNotSelected} />
                </div>
                <ProfileStatusWithHooks fullName={props.profile.fullName} status={props.status} updateStatus={props.updateStatus} />
            </div>
            <div className={cl.jobContainer}>
                <div className={cl.readyForJob}>
                    <div className={cl.lookingForAJob}>     :
                        <img className={cl.lookingForAJobImg} src={props.profile.lookingForAJob === true ? galochka : krest} />
                    </div>
                    <div className={cl.lookingForAJobDescription}>
                        {!props.profile.lookingForAJobDescription ? "     " : props.profile.lookingForAJobDescription}
                    </div>
                </div>
                <div className={cl.contacts}>
                    <div className={!props.profile.contacts.facebook ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.facebook} ${cl.social}`} href={props.profile.contacts.facebook}>
                            <img className={cl.contactImg} src={facebook} />Facebook</a></div>
                    <div className={!props.profile.contacts.github ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.github} ${cl.social}`} href={props.profile.contacts.github}>
                            <img className={cl.contactImg} src={github} />Github</a></div>
                    <div className={!props.profile.contacts.instagram ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.instagram} ${cl.social}`} href={props.profile.contacts.instagram}>
                            <img className={cl.contactImg} src={instagram} />Instagram</a></div>
                    <div className={!props.profile.contacts.mainLink ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.mainLink} ${cl.social}`} href={props.profile.contacts.mainLink}>
                            <img className={cl.contactImg} src={mainLink} />MainLink</a></div>
                    <div className={!props.profile.contacts.twitter ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.twitter} ${cl.social}`} href={props.profile.contacts.twitter}>
                            <img className={cl.contactImg} src={twitter} />Twitter</a></div>
                    <div className={!props.profile.contacts.vk ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.vk} ${cl.social}`} href={props.profile.contacts.vk}>
                            <img className={cl.contactImg} src={vk} />Vk</a></div>
                    <div className={!props.profile.contacts.website ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.website} ${cl.social}`} href={props.profile.contacts.website}>
                            <img className={cl.contactImg} src={website} />Website</a></div>
                    <div className={!props.profile.contacts.youtube ? `${cl.none}` : `${cl.socialContainer}`}>
                        <a className={`${cl.youtube} ${cl.social}`} href={props.profile.contacts.youtube}>
                            <img className={cl.contactImg} src={youtube} />Youtube</a></div>
                </div>
            </div>
        </div>
    );
};

export default ProfileInfo;
